.form-container{
  display: grid;
  grid-template-columns: 2fr 3fr;
  justify-content: center;
  align-content: center;
  width:60%;
  margin:5% auto;
}

.left-container{
  position: relative;
  border-radius:10px 0 0 10px;
  text-align: center;
  box-sizing: border-box;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border: 1px solid #ddd;
  border-right:0;
}

.left-container img{
  width: 100%;
  height: 100%;
  aspect-ratio:2/3;
  z-index: -1;
  object-fit:cover;
  border-radius: 10px 0 0 10px;
  margin: 0;
  overflow: hidden;
}
.left-container .form-text{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-70%);
  color:#fff;
  font-size:1.5em;
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}
.left-container h2{
  border-bottom: 3px dotted;
  margin:10px;
}

.right-container{
  box-sizing: border-box;
  display: grid;
  place-content: center;
  justify-items: center;
  align-items: center;
  padding: 1em;
  background-color: #fff;
  border-radius: 0 10px 10px 0;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border: 1px solid #ddd;
}


.login-form .signup-form, .forgot-password{
  box-sizing: border-box;
  padding: 5px;
  text-align: center;
  background-color: #ffffff; 
}

.input-container {
  position: relative;
  width: 95%;
  height:40%;
  margin:0 auto;
  margin-bottom: 15px;
  /* border:  */
  padding: 0 0  0 8px;
  border-radius: 5px;
}
.input-container:hover{
  box-shadow: 0 0 5px rgba(52, 152, 219, 0.5);
}

.input-container input {
  width: calc(100% - 30px);
  padding:10px;
  outline: none;
  border:none;
  font-size: 18px;
  box-sizing: border-box;
}

.input-container input::placeholder{
  font-size: 20px;
}

.email-field , .password-field{
  display: flex;
}


.input-container svg {
  position: relative;
  top:5px;
  right:5px;
  color: #3498db;
}

.btn {
  width: 100%;
  margin: auto;
  padding: 10px;
  background-color: #3498db;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 18px;
}
.log-in-btn{
  background-color: #2980b9;
}

.log-in:hover {
  background-color: #2980b9;
}

.sign-up-btn{
  background-color: #2dbe32;
}
.sign-up-btn:hover{
  background-color: #26912a;
}
.sign-up-btn:disabled{
  cursor: not-allowed;
  background-color: #989696;
}


a {
  display: block;
  margin-top: 15px;
  text-align: center;
  color: #3498db;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.loader-container{
  position: absolute;
  top: 0;
  left: 0;
  background-color: #2d2d2d7d;
  text-align: center;
  width: 100%;
  height: 100%;
  z-index: 5;

}
.loader {
  position: absolute;
  top:50%;
  left:50%;
  transform: translate(-50%,-50%);
  color: #efefef;
  width: 6px;
  aspect-ratio: 1;
  border-radius: 50%; 
  animation:
    l37-1 .75s infinite linear alternate,
    l37-2 1.5s  infinite linear; 
}

.email-loader svg{
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(360deg);
  }
}

.warning{
  border:2px solid #9a2a2a;
}
.success{
  border:2px solid #167d1a;
}
h4{
  display:flex;
  font-size: 0.7em;
  color:rgb(219, 53, 53);
  margin:10px;
}

.fade-in {
  max-height: 100px;
  overflow: hidden;
  transition: max-height 0.1s ease-in-out;
}

.hidden {
  max-height: 0;
  margin-top: 0;
  margin-bottom: 0;
  padding-top: 0;
  padding-bottom: 0;
}



@keyframes l37-1 {
  0%,
  20% {box-shadow:30px 0 0 3px, 10px 0 0 3px, -10px 0 0 3px,-30px 0 0 3px}
  60%,
  100% {box-shadow:12px 0 0 3px, 14px 0 0 6px, -14px 0 0 6px,-12px 0 0 3px}
}

@keyframes l37-2 {
  0%,25%   {transform: rotate(0)}
  50%,100% {transform: rotate(.5turn)}
}


@media screen and (max-width:1000px){
  .form-container{
    width: 75%;
  }
}

@media screen and (max-width:750px) {
  .form-container{
    width:85%;
  }
}

@media screen and (max-width:650px) {
  .form-container{
    grid-template-columns: 1fr;
    grid-template-rows: 75px 1fr;
    gap:15px;
  }
  .left-container{
    border-radius: 5px;
  }
  .left-container img{
    border-radius: 5px;
  }
  .left-container p{
    font-size: 0.85em;
    top:40%;
  }
  .right-container{
    border-radius: 5px;
  }
}